
import { defineComponent, reactive } from "vue";
import { useRouter } from "vue-router";
import { gpf } from "@/utils/global-functions";
import { api } from "@/utils/api-config";

export default defineComponent({
  components: {},
  setup() {
    const router = useRouter();
    const catPPPK = reactive({
      list: [],
      dataPG: {
        kode: "",
        title: "",
        nilaiPg: 0,
        jmlSoal: 0,
        durasi: 0,
        jurusan: [],
        selectJurusan: "",
      },
    });

    const saveUrl = async (data: any) => {
      // gpf.gLoading.show();
      const hide = () => gpf.gLoading.hide();

      const url =
        (await api.getConfigFeatureMember(
          "memberPassingGrade",
          "featurePassingGrade"
        )) + data.kode;
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;
      catPPPK.dataPG = rs;
      if (gpf.trimText(rs.jurusan)) {
        catPPPK.dataPG.jurusan = rs.jurusan;
        catPPPK.dataPG.selectJurusan = rs.jurusan[0].kode;
      } else {
        catPPPK.dataPG.jurusan = [];
        catPPPK.dataPG.selectJurusan = "";
      }

      gpf.showModal("#modal-cat-pppk");
      gpf.gLoading.hide();
    };

    const gotoTryoutExam = () => {
      gpf.hideModal("#modal-cat-pppk");
      // gpf.gLoading.show();

      const kode = catPPPK.dataPG.kode;

      setTimeout(() => {
        if (catPPPK.dataPG.jurusan.length) {
          router.push(
            "cat-pppk/" + kode + "?jurusan=" + catPPPK.dataPG.selectJurusan
          );
        } else {
          router.push("cat-pppk/" + kode);
        }
      }, 700);
    };

    const getData = async () => {
      cash("#loadingCatPppk").show();
      const hide = () => cash("#loadingCatPppk").hide();

      const url =
        (await api.getConfigFeatureMember(
          "memberJenisTryout",
          "featureJenisTryout"
        )) + "pppk";
      const hitApi = await api.hitApiGet(url, hide);
      const rs = hitApi.data.response;
      catPPPK.list = rs;
    };

    getData();

    return {
      saveUrl,
      gotoTryoutExam,
      catPPPK,
      getData,
    };
  },
  methods: {
    changeTS(v: any, f: any) {
      const val = v.detail.args[1].key;
      const ex: any = this.catPPPK.dataPG;
      ex[f] = val;

      console.log("this.catPPPK ", this.catPPPK);
    },
  },
});
